import axios from 'axios';

axios.defaults.baseURL = 'https://pixabay.com/api';
const API_KEY = '21790462-d81f7d941fc30814a1e9b910b';

const refs = {
  cardMarkup: document.querySelector('.gallery_item-img'),
};

export const getImage = async () => {
  const newImg = await axios.get(`/?key=${API_KEY}&q=developer&image_type=photo`);
  console.log(newImg);
  const response = await newImg.data.hits[1].largeImageURL;
  refs.cardMarkup.src = response;

  return response;
};
getImage();
